import React, { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import {
  Home,
  Building,
  VideoIcon,
  Blocks,
  ZoomIn,
  LogOut,
  LayoutDashboard,
  Menu,
  X,
  Settings,
} from "lucide-react";

function Sidebar() {
  const location = useLocation();
  const [isOpen, setIsOpen] = useState(false);

  const isActive = (paths) =>
    paths.some((path) => location.pathname.startsWith(path));
  const isActivePrefix = (prefix) => {
    return location.pathname.startsWith(prefix);
  };
  function isActivePath(paths) {
    const currentPath = window.location.pathname; // or use router.pathname if you're in Next.js

    return paths.some((path) => {
      if (path instanceof RegExp) {
        return path.test(currentPath);
      }
      return currentPath.startsWith(path); // for static string matches
    });
  }

  return (
    <>
      <div className="d-md-none p-3 bg-dark text-white d-flex flex-column gap-2 vh-100   justify-content-between align-items-center">
        <div className="d-flex flex-column gap-2 justify-content-center align-items-center">
          <button
            className="btn btn-dark text-white"
            onClick={() => setIsOpen(!isOpen)}
          >
            {isOpen ? <X size={24} /> : <Menu size={24} />}
          </button>

          <ul className="list-unstyled dash_scroll">
            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/dashboard"]) ? "color_primary" : ""
              }`}
            >
              <Link
                to="/admin/dashboard"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <Home size={20} />
              </Link>
            </li>

            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/company"]) ? "color_primary" : ""
              }`}
            >
              <Link
                to="/admin/company"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <Building size={20} />
              </Link>
            </li>

            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/video/list", "/admin/video/add"])
                  ? "color_primary"
                  : ""
              }`}
            >
              <Link
                to="/admin/video/list"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <VideoIcon size={20} />
              </Link>
            </li>

            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/module/list", "/admin/module/add"])
                  ? "color_primary"
                  : ""
              }`}
            >
              <Link
                to="/admin/module/list"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <Blocks size={20} />
              </Link>
            </li>

            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/userzoomdetail", "/admin/userzoometting"])
                  ? "color_primary"
                  : ""
              }`}
            >
              <Link
                to="/admin/userzoomdetail"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <ZoomIn size={20} />
              </Link>
            </li>
            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/userzoomdetail", "/admin/userzoometting"])
                  ? "color_primary"
                  : ""
              }`}
            >
              <Link
                to="/admin/userzoomdetail"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <ZoomIn size={20} />
              </Link>
            </li>
          </ul>
        </div>
        <div
          className="p-2 rounded bg-danger d-flex align-items-center gap-2  text-white"
          style={{ cursor: "pointer" }}
        >
          <Link
            to="/admin/logout"
            className="text-white text-center d-flex align-items-center justify-content-center gap-2 text-decoration-none w-100"
          >
            <LogOut size={20} />
          </Link>
        </div>
      </div>

      {/* Sidebar */}
      <div
        className={`bg-dark side_bar_admin text-white vh-100 shadow-lg p-4 position-sticky top-0 start-0 ${
          isOpen ? "d-flex" : "d-none"
        } d-md-flex flex-column justify-content-between`}
        style={{ width: "300px", flexShrink: "0" }}
      >
        <div className="d-flex flex-column gap-3">
          <div className="side_logo">
            <img
              className="w-100 h-100 object-fit-cover"
              src={require("../../assets/admin/images/logo.png")}
              alt="logo"
            />
          </div>

          <div className="d-flex align-items-center gap-2">
            <LayoutDashboard size={26} />
            <h4 className="m-0 fw-bold">Admin Panel</h4>
          </div>

          <ul className="list-unstyled dash_scroll">
            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/dashboard"]) ? "color_primary" : ""
              }`}
            >
              <Link
                to="/admin/dashboard"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <Home size={20} /> <span>Dashboard</span>
              </Link>
            </li>

            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/company"]) ? "color_primary" : ""
              }`}
            >
              <Link
                to="/admin/company"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <Building size={20} /> <span>Registered Companies</span>
              </Link>
            </li>

            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/video/list", "/admin/video/add"])
                  ? "color_primary"
                  : ""
              }`}
            >
              <Link
                to="/admin/video/list"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <VideoIcon size={20} /> <span>Video Management</span>
              </Link>
            </li>

            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/module/list", "/admin/module/add"])
                  ? "color_primary"
                  : ""
              }`}
            >
              <Link
                to="/admin/module/list"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <Blocks size={20} /> <span>Modules</span>
              </Link>
            </li>

            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActive(["/admin/userzoomdetail", "/admin/userzoometting"])
                  ? "color_primary"
                  : ""
              }`}
            >
              <Link
                to="/admin/userzoomdetail"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <ZoomIn size={20} /> <span>Zoom Meeting</span>
              </Link>
            </li>

            <li
              className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                isActivePath([
                  "/admin/duediligencecategoryList",
                  "/admin/duediligencecategory/add",
                  /^\/admin\/duediligencecategorytip\/add\/\d+$/,
                ])
                  ? "color_primary"
                  : ""
              }`}
            >
              <Link
                to="/admin/duediligencecategoryList"
                className="text-white d-flex align-items-center gap-2 text-decoration-none w-100"
              >
                <Home size={20} /> <span>Data Rooms Category</span>
              </Link>
            </li>
            <li
              className={`p-2 rounded d-flex flex-column gap-2 mb-2 ${
                isActivePrefix([
                  "/admin/setting/dataroompaymentadd",
                  "/admin/setting/paymentdiscountlist",
                ])
                  ? "color_primary"
                  : ""
              }`}
            >
              {/* Main Menu Item */}
              <div className="d-flex align-items-center gap-2 text-white">
                <Settings size={20} /> <span>Setting</span>
              </div>

              {/* Sub Menu List */}
              <ul className="ms-4 list-unstyled">
                <li
                  className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                    isActive(["/admin/setting/dataroompaymentadd"])
                      ? "active "
                      : ""
                  }`}
                >
                  <Link
                    to="/admin/setting/dataroompaymentadd"
                    className="text-white d-flex align-items-center gap-2 text-decoration-none py-1 submenu_new"
                  >
                    DataRoom Due Diligence Academy
                  </Link>
                </li>
                <li
                  className={`p-2 rounded d-flex align-items-center gap-2 mb-2 ${
                    isActive(["/admin/setting/paymentdiscountlist"])
                      ? "active "
                      : ""
                  }`}
                >
                  <Link
                    to="/admin/setting/paymentdiscountlist"
                    className="text-white d-flex align-items-center gap-2 text-decoration-none py-1 submenu_new"
                  >
                    Discount Codes
                  </Link>
                </li>
              </ul>
            </li>
          </ul>
        </div>

        <div
          className="p-2 rounded bg-danger d-flex align-items-center gap-2  text-white"
          style={{ cursor: "pointer" }}
        >
          <Link
            to="/admin/logout"
            className="text-white text-center d-flex align-items-center justify-content-center gap-2 text-decoration-none w-100"
          >
            <LogOut size={20} /> <span>Logout</span>
          </Link>
        </div>
      </div>
    </>
  );
}

export default Sidebar;
